* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.search-form {
  min-height: 70px;
  text-align: center;
  background: linear-gradient(90deg, #5919b3 0%, #d10eee 52%, #ff0070 100%);
  justify-content: center;
  padding: 20px 0;
  display: flex;
}

.search-field {
  outline: unset;
  min-width: 300px;
  min-height: 30px;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .15));
  border: 1px solid rgba(255, 255, 255, .3);
  border-radius: 4px;
  font-family: inherit;
  font-style: italic;
}

.search-btn {
  color: #fafafa;
  cursor: pointer;
  min-width: 80px;
  min-height: 30px;
  letter-spacing: .06em;
  background-color: #743bdf;
  border: none;
  border-radius: 4px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.88;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

.gallery {
  flex-wrap: wrap;
  flex-basis: calc(33.3333% - 20px);
  justify-content: center;
  align-items: center;
  gap: 15px;
  display: flex;
}

.photo-card {
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.info {
  justify-content: center;
  align-items: center;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.load-more {
  color: #fafafa;
  cursor: pointer;
  min-width: 150px;
  min-height: 30px;
  letter-spacing: .06em;
  background-color: #743bdf;
  border: none;
  border-radius: 4px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.88;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.load-more:focus, .load-more:hover, .search-btn:focus, .search-btn:hover {
  background-color: #8947f1;
}

